
import  {observable, computed, action} from "mobx";
import axios from 'axios';


const URL = 'http://yousefmohsen.dk:4000/team/all';
class DataStore {

    @observable Teams = [];


    constructor() {
     //  this.testMethod();
        //this.getFlightByDestination("CPH");

        this.fetchTeamList();

    }

    @action
    setTeams(newTeamList){
        this.Teams = newTeamList;
        console.log(this.Teams);
        

    }
    fetchTeamList(){
        var st = this.setTeams;
                axios.get(URL)
                .then( (response)=> {
                    //console.log(response.data);
                    
                    this.setTeams(response.data);
                })
                .catch(function (error) {
                  console.log(error);
                });
              }




              
sendNewMessage(formData){

      axios.post('http://yousefmohsen.dk:4000/msg/new', formData)
      .then((result,err) => {
          alert("Beskeden er blevet sendt!");

      },(err)=>{
        alert("Fejl fra serveren. Beskeden er ikke sendt.")
        console.log("ERROR :/")
      });

    }



createNewTeam(newTeam){

  return axios.post('http://localhost:4000/team/new', {
      newTeam:newTeam
    })
     
}    
}

export default new DataStore();

      























